.weui_tabbar {
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  z-index: 100;
  bottom: 0;
  width: 100%;
  background-color: #f7f7fa;
}
.weui_tabbar:before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 1px;
  border-top: 1px solid #979797;
  color: #979797;
  transform-origin: 0 0;
  transform: scaleY(0.5);
}
.weui_tabbar_item {
  display: block;
  -ms-flex: 1;
      flex: 1;
  padding: 7px 0 0;
  -webkit-tap-highlight-color: transparent;
}
.weui_tabbar_item.weui_bar_item_on .weui_tabbar_label {
  color: #09BB07;
}
.weui_tabbar_icon {
  margin: 0 auto;
  width: 24px;
  height: 24px;
}
.weui_tabbar_icon img {
  display: block;
  width: 100%;
  height: 100%;
}
.weui_tabbar_icon + .weui_tabbar_label {
  margin-top: 5px;
}
.weui_tabbar_label {
  text-align: center;
  color: #888;
  font-size: 12px;
}
.weui_tab {
  position: relative;
  height: 100%;
}
.weui_tab_bd {
  box-sizing: border-box;
  height: 100%;
  padding-bottom: 55px;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.weui_tab_bd_item {
  display: none;
}
.weui_tab_bd_item_active {
  display: block;
}
.vux-reddot,
.vux-reddot-border,
.vux-reddot-s {
  position: relative;
}
.vux-reddot:after,
.vux-reddot-border:after,
.vux-reddot-s:after {
  content: '';
  position: absolute;
  display: block;
  width: 8px;
  height: 8px;
  background-color: #f74c31;
  border-radius: 5px;
  right: -3px;
  top: -3px;
  background-clip: padding-box;
}
.vux-reddot-border:before {
  content: '';
  position: absolute;
  display: block;
  width: 8px;
  height: 8px;
  background-color: #fff;
  border-radius: 5px;
  right: -4px;
  top: -4px;
  background-clip: padding-box;
  padding: 1px;
}
.vux-reddot-s:after {
  width: 6px;
  height: 6px;
  top: -5px;
  right: -5px;
}
.weui_tabbar_icon {
